.news-card {
    position: relative;
    display: flex;
    flex-direction: column;
    background: $white;
    text-decoration: none;
    padding: 0;
    min-height: vw(640);
    &:hover {
        .news-card__btn {
            background-color: $fontBlack;
        }
    }
    @include max(md) {
        min-height: vw2(640);
    }
    &__link {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 5;
    }
    &__img  {
        width: 100%;
        height: vw(297);
        flex-basis: vw(297);
        flex-shrink: 0;
        flex-grow: 0;
        picture, img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        @include max(md) {
            height: vw2(297);
            flex-basis: vw2(297);
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        padding: vw(38) vw(25) vw(26) vw(38);
        flex-basis: auto;
        flex-shrink: 1;
        flex-grow: 1;
        @include max(md) {
            padding: vw2(38) vw2(25) vw2(26) vw2(38);
        }
    }

    &__title {
        color: $fontBlack;
        font-size: vw(32);
        font-weight: 500;
        line-height: 0.97;
        text-transform: uppercase;
        @include max(md) {
            font-size: vw2(32);
        }       
    }

    &__info.text-block {
        margin-top: vw(18);
        margin-bottom: vw(30);
        @include max(md) {
            margin-top: vw2(18);
            margin-bottom: vw2(30);
            ul, li, p, span {
                font-size: vw2(18);
            }
        }  
    }

    &__bottom {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        width: 100%;
        gap: vw(50);
        @include max(md) {
            gap: vw2(20);
        }
    }
    &__date {
        font-size: vw(14);
        font-weight: 600;
        line-height: 1.25;
        color: $fontBlack;
        opacity: 0.2;
        @include max(md) {
            font-size: vw2(14);
        } 
    }
    &__btn {
        svg use {
            stroke: $white !important;
        }
    }
}