h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}
h1, h2 {
    color: inherit;
    line-height: inherit;
    font-size: inherit;
    letter-spacing: inherit;
    font-weight: inherit;
}
html {
	font-family: $mainFont;
	font-weight: 500;
	font-size: 16px;
	line-height: 27px;
	color: #031B4B;
    scroll-behavior: smooth;
}
body.Bodymotionless {
	width: 100vw;
	min-height: 100vh;
	overflow: hidden;
  }

* {
	margin: 0;
	padding: 0;
    box-sizing: border-box;
}
html,
body {
    height: 100%;
    display: flex;
	flex-direction: column;
	min-height: 100%;
}
a, button {
    cursor: pointer;
    text-decoration: none;
}
.site-footer{
	flex: 0 0 auto;
}

.pin-spacer {
    max-width: 100%;
}

.container {
    position: relative;
	box-sizing: border-box;
	width: vw(1920);
	max-width: calc(100% - vw(50));
	margin: 0 auto;
    @include max(md) {
        width: 100%;
        max-width: calc(100% - vw2(40));
    }
}

.btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: max-content;
    color: $white;
    min-height: vw(50);
    padding-left: vw(20);
    padding-right: vw(20);
    color: #FFF;
    text-align: center;
    font-size: vw(16);
    font-style: normal;
    font-weight: 200;
    line-height: normal;
    text-decoration: none;
    border: none;
    min-width: vw(220);
    cursor: pointer;
    background: $blue;
    border-radius: 0;
    transition: all $tr-time;
    @include max(md) {
        max-width: 100%;
        min-width: vw2(300);
        min-height: vw2(50);
        padding-left: vw2(20);
        padding-right: vw2(20);
        font-size: vw2(14);
        font-weight: 400;
    }
    p {
        @include max(sm) {
            font-size: vw2(14);
            font-style: normal;
            font-weight: 400;
            line-height: normal;
        }  
    }
    &.btn--circle {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        width: vw(59);
        height: vw(59);
        min-width: vw(59);
        min-height: vw(59);
        border-radius: 50%;
        transition: all $tr-time;
        padding: 0;
        @include max(md) {
            width: vw2(50);
            height: vw2(50);
            min-width: vw2(50);
            min-height: vw2(50);          
        }
        svg {
            width: vw(11);
            height: vw(9);
            use {
                stroke: $fontBlack;
                width: 100%;
                height: 100%;
            }
            @include max(md) {
                width: vw2(11);
                height: vw2(9);              
            }         
        }
        &:hover {
            background: $blueLight;
            transition: all $tr-time-fast;
            svg use {
                stroke: $white;
            }
        }
        &__blue {
            background: $blueLight;
            svg use {
                stroke: $white;
            }
            &:hover {
                background: $black;
                transition: all $tr-time-fast;
                svg use {
                    stroke: $white;
                }
            }
        }
        &__download {
            transform: rotate(90deg);
            background: $blueLight;
            svg {
                use {
                    stroke: $white;  
                }
            }
            &:hover {
                background: $black;
                transition: all $tr-time-fast;
            }
        }
        &__black {
            background: $black;
            svg use {
                stroke: $white
            }
            &:hover {
                background: $blueLight;
                transition: all $tr-time-fast;
            }
        }
    }
    &--box {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        flex-flow: column nowrap;
        width: vw(411);
        height: vw(221);
        padding: vw(20);
        background: $blueLight;
        text-decoration: none;
        transition: all $tr-time;
        @include max(md) {
            width: vw2(260);
            height: vw2(140);
            padding: vw2(20);
        }

        &:hover {
            background: #fff;
            transition: all $tr-time-fast;
            .btn--box__text {
                transition: all $tr-time-fast;
                color: $blueLight;
            }
            .btn{
                transition: all $tr-time-fast;
                background: $blueLight;
            }
            svg use {
                stroke: $white;
            }
        }
        &__text {
            position: relative;
            display: inline-block;
            transition: all $tr-time;
            color: #FFF;
            font-size: vw(14);
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            text-transform: uppercase; 
            width: 100%;
            text-align: left; 
            @include max(md) {
                font-size: vw2(22);
                font-style: normal;
                line-height: 1.2;
            }           
        }
    }
}

