.swiper-buttons {
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: vw(60);
    width: 100%;
    max-width: vw(132);
    top: auto;
    right: vw(22);
    bottom: vw(27);
    overflow: visible;
    z-index: 15;
    @include max(md) {
        left: auto;
        right: vw2(20);
        bottom: vw2(20);
        max-width: vw2(110);
        height: vw2(50);
    }
    .swiper-button-prev,.swiper-button-next {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #fff;
        border-radius: 50%;
        width: vw(59);
        height: vw(59);
        min-width: vw(59);
        min-height: vw(59);
        top: 0;
        transition: all $tr-time;
        @include max(md) {
            width: vw2(50);
            height: vw2(50);
            min-width: vw2(50);
            min-height: vw2(50);  
            top: calc(50% - vw2(5));        
        }
        svg {
            width: vw(14);
            height: vw(12);
            use {
                width: 100%;
                height: 100%;
                stroke: $fontBlack;
            }
            @include max(md) {
                width: vw2(13);
                height: vw2(11);             
            }            
        }
        &:hover {
            background: $blueLight;
            transition: all $tr-time-fast;
            svg {
                use {
                    stroke: $white;
                }           
            }
        }
        &.swiper-button-disabled {
            opacity: 0.2;
        }
        &::after {
            font-size: 0;
            display: none;
        }
  
  
    }
    .swiper-button-prev {
       left: 0;
       svg {
        transform: rotate(180deg);
       }
  
    }
    .swiper-button-next {
        right: 0;
  
    }

}
.layout {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: stretch;
    width: 100%;
    @include max(md) {
        flex-flow: column nowrap;
    }
    &__col {
        position: relative;
        display: flex;
        padding-top: vw(20);
        flex-flow: column nowrap;
        &:nth-child(1) {
            flex:1 0 66.666%;
            max-width: 66.666%;

            @include max(md) {
                flex: 1 0 100%;
                max-width: 100%;
                border-right: none;
                padding-right: vw(0);
                padding-left: vw(0);
            }
        }
        &:nth-child(2) {
            flex: 1 0 33.333333%;
            max-width: 33.333333%;
            @include max(md) {
                flex:1 0 100%;
                max-width: 100%;
                border-right: none;
                padding-right: vw(0);
                padding-left: vw(0);
            }
        }
    }
    &--trio {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        width: 100%; 
        @include max(md) {
            flex-flow: column nowrap;
        }  
        &__col {
            position: relative;
            display: flex;
    
            &:nth-child(1) {
                flex:1 0 33.333333%;
                max-width: 33.333333%;
                @include max(md) {
                    flex:1 0 100%;
                    max-width: 100%;
                    border-right: none;
                }  
            }
            &:nth-child(2) {
                flex:1 0 33.333333%;
                max-width: 33.333333%;
                @include max(md) {
                    flex:1 0 100%;
                    max-width: 100%;
                    border-right: none;
                }  
            }
            &:nth-child(3) {
                flex:1 0 33.333333%;
                max-width: 33.333333%;
                @include max(md) {
                    flex:1 0 100%;
                    max-width: 100%;
                    border-right: none;
                    padding-right: 0;
                    padding-left: 0;
                }  
            }
        }
    }
    &--reverse {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        width: 100%;
        &__col {
            position: relative;
            display: flex;
            &:nth-child(1) {
                flex:1 0 33.333333%;
                max-width: 33.333333%;
                border-right: 1px solid #EFEFEF;
                padding-right: vw(30);
                padding-left: vw(30);
                @include max(md) {
                    flex:1 0 100%;
                    max-width: 100%;
                    border-right: none;
                    padding-right: 0;
                    padding-left: 0;
                }  
            }
            &:nth-child(2) {
                flex:1 0 66.666%;
                max-width: 66.666%;
                padding-right: vw(30);
                @include max(md) {
                    flex:1 0 100%;
                    max-width: 100%;
                    border-right: none;
                    padding-right: 0;
                    padding-left: 0;
                }  
            }
        }
    }
    &--one {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        width: 100%; 
        &__col {
            position: relative;
            display: flex;
            
            &:nth-child(1) {
                flex:1 0 100%;
                max-width: 100%;
                border-right: none;
                padding-right: vw(30);
                padding-left: vw(30);
                @include max(md) {
                    padding-right: 0;
                    padding-left: 0;
                } 
            }
        }        
    }
}

@keyframes rotateCircle {
	0% {
		transform: rotate(0)
	}

	100% {
		transform: rotate(360deg)
	}
}

.start {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    &__bg {
        position: absolute;
        bottom: vw(-10);
        left: 0;
        width: 100%;
        display: block;
        z-index: 10;
        img{
            display: block;
            width: 100%;
            object-fit: cover;
            object-position: center center;
        }
    }
    &__section {
        z-index: 5;
		@include max(md) {
			margin-top: vw2(70);	
			height: calc(100vh - vw2(70));
            max-height: vw2(600);
		}
    }
    &__slider {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        overflow: hidden;
        width: 100%;
        height: 100%;
        .swiper-buttons {
            @include max(md) {
                bottom: vw2(39);
            }
        }
    }
    &__slide {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100vh;
        flex-flow: column nowrap;
        @include max(md) {
            min-height: 100%;
        }
        &__picture  {
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            width: 100%;
            height: 100vh;
            picture, img {
                position: absolute;
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: center center;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }
        }
        &__video {
            position: relative;
            width: 100%;
            height: 100vh;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            video {
                position: relative;
                width: 100%;
                height: 100%;
                display: block;
                object-fit: cover;
                object-position: center center;
            }
        }
        &__desc {
            position: absolute;
            top: vw(112);
            left: vw(25);
            z-index:  10;
            opacity: 0;
            @include max(md) {
                top: vw2(20);
                left: vw2(20);
                opacity: 1;
            }
        }
        &__btn {
            position: absolute;
            bottom: vw(33);
            left: vw(25);
            z-index:  20;
            opacity: 0;
            @include max(md) {
                bottom: vw2(20);
                left: vw2(20);
                opacity: 1;
            }
        }
        
        &__title {
            position: relative;
            max-width: vw(884);
            margin-bottom: vw(25);
            @include max(md) {
                max-width: 95%;
                margin-bottom: vw2(25);
            }
        }
        &__text {
            position: relative;
            max-width: vw(353);
            @include max(md) {
                max-width: 70%;
            }
        }

    }
    
}

