/* для элемента input c type="checkbox" */
.checkbox {
    .custom-checkbox {
        position: absolute;
        z-index: -1;
        opacity: 0;
    }
    label {
        position: relative;
        display: inline-flex;
        align-items: center;
        user-select: none;
        color: $fontBlack;
        font-size: vw(18);
        font-style: normal;
        font-weight: 400;
        line-height: 140.625%;
        &::before {
            content: '';
            display: inline-block;
            width: vw(26);
            height: vw(26);
            min-width: vw(26);
            min-height: vw(26);
            border-radius: vw(4);
            border: 1px solid rgba(0, 0, 0, 0.2);
            margin-right: vw(12);
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 60% 60%;
        
        }
    }
    label:has(.custom-checkbox:not(:disabled):not(:checked)):hover::before {
        border-color: $blueHover;
    }
    
    label:has(.custom-checkbox:not(:disabled):active)::before {
        background-color: $blueHover;
        border-color: $blueHover;
    }
    
    label:has(.custom-checkbox:focus)::before {
        box-shadow: 0 0 0 0 rgba(0, 123, 255, 0);
    }
    
    label:has(.custom-checkbox:focus:not(:checked))::before {
        border-color: $blueHover;
    }
    
    label:has(.custom-checkbox:checked)::before {
        border-color: rgba(0, 0, 0, 0.2);
        background-color: #fff;
        background-image: url(../img/custom-checkbox.png);
    }
    
    label:has(.custom-checkbox:disabled)::before {
        background-color: #e9ecef;
    }
}

.input-file {
    width: 100%;
    display: block;
    color: $fontBlack;
    font-size: vw(18);
    font-style: normal;
    font-weight: 400;
    line-height: 140.625%;
    width: 100%;
    border: none;
    background: transparent;
    cursor: pointer;
    &-row {
        display: inline-block;
        width: 100%;
    }
    span {
        display: block;
        color: $fontBlack;
        font-size: vw(18);
        font-style: normal;
        min-height: vw(60);
        padding-right: vw(18);
        padding-top: vw(20);
        font-weight: 400;
        line-height: 140.625%;
        width: 100%;
        height: 100%;
        background: transparent;
        border: 0px solid rgba(255, 255, 255, 1);
        border-bottom:1px solid rgba(42, 42, 42, 0.2);
        transition: all $tr-time;
    }
    input[type=file] {
        position: absolute;
        z-index: -1;
        opacity: 0;
        display: block;
        width: 0;
        height: 0;
    }
    /* Focus */
    input[type=file]:focus + span {
        border-bottom: 1px solid $blue;
        transition: all $tr-time;
    }
     
    /* Hover/Active */
    &:hover span {
        border-bottom: 1px solid $blue;
        transition: all $tr-time;
    }
    &:active span {
        border-bottom: 1px solid $blue;
        transition: all $tr-time;
    }
     
    /* Disabled */
    input[type=file]:disabled + span {
        border-bottom: 1px solid $red;
        transition: all $tr-time;
    }
     
    /* Список файлов */
    &-list {
        padding: vw(10) 0;
    
    }
    
    &-list-item {
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        color: $fontBlack;
        font-size: vw(18);
        font-style: normal;
        font-weight: 400;
        line-height: 140.625%;
        margin-bottom: vw(10);
    }
    &-list-name {
        display: flex;
        justify-content: flex-start;
        align-items: flex-end;
        color: $fontBlack;
        font-size: vw(18);
        font-style: normal;
        font-weight: 400;
        line-height: 140.625%;
    }
    &-list-remove {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        color: red;
        text-decoration: none;
        margin-left: vw(5);
    }
}
 


@keyframes rotationClose{
	0% {
		transform-origin: 50% 50%;
		transform: rotate(0deg);
	}

	100% {
		transform-origin: 50% 50%;
		transform: rotate(45deg);
	}
}

@keyframes rotationOpen {
	0% {
		transform-origin: 50% 50%;
		transform: rotate(45deg);
	}

	100% {
		transform-origin: 50% 50%;
		transform: rotate(0deg);
	}
}


